import { IProfileData } from "./Profile";

/** API for accessing persisted profile data. */
export class UserData {

    /** Returns the names of all registered profiles. @see getProfileData to retrieve the corresponding profile data. */
    static getProfileNames(): string[] {
        // TODO: read from IndexedDB
        let profiles: string[] | undefined; 
        const persistedData = localStorage.getItem('profiles') as string; 
        if (persistedData) {
            try { profiles = JSON.parse(persistedData); }
            catch (e) {}
        }
        if (!(profiles instanceof Array))
            profiles = [ 'Carlos', 'Derek', 'Luke', 'Marco', 'Matt', 'Ruy' ]; 
        return profiles; 
    }

    /** Returns the data for the specified profile (or creates a new profile). */
    static getProfileData(profileName: string): Readonly<IProfileData> {
        if (!profileName)
            throw new Error('Missing profile argument.'); 
        // TODO: read from IndexedDB
        let profileData: IProfileData | undefined; 
        const persistedData = localStorage.getItem('profile_' + profileName) as string; 
        if (persistedData) {
            try { profileName = JSON.parse(persistedData); }
            catch (e) {}
        }
        if (typeof(profileData) !== 'object')
            profileData = { name: profileName, climbs: [], joined: new Date().toISOString() }; 
        return profileData; 
    }

    static setProfileData(profileData: IProfileData) {
        if (!profileData || !profileData.name)
            return; 
        // TODO: write to IndexedDB
        localStorage.setItem(profileData.name, JSON.stringify(profileData)); 
    }
}

