import { faHourglass } from '@fortawesome/free-solid-svg-icons';
import { DifficultyLabels } from '../components/ClimbHistoryListing';
import ResponsiveGrid from '../components/ResponsiveGrid';
import Section from '../components/Section';
import Tile from '../components/Tile';
import Profile, { IClimbInfo } from '../Profile';

function RecordClimbContent() {
  const climb = getTemplate(); 

  return (<>

    <Section headline={ { text: "You are attempting this climb", icon: faHourglass } }>
      <table className="listing" cellPadding={5} cellSpacing={0}>
          <tbody>
              <tr>
                  <th style={{ width: '20%' }}>Wall</th>
                  <td>
                    <input type="text" maxLength={50} defaultValue={ climb.wall } />
                  </td>
              </tr>
              <tr>
                  <th>Difficulty</th>
                  <td>
                    <select size={1} defaultValue={climb.difficulty}>{
                      DifficultyLabels.map((l,i) => <option value={i} key={i}>{l}</option>)
                    }</select>
                  </td>
              </tr>
              <tr>
                  <th>Technique</th>
                  <td>
                    <input type="text" maxLength={50} defaultValue={ climb.technique } />
                  </td>
              </tr>
          </tbody>
      </table>

      <p>Take care and record your result:</p>
      <ResponsiveGrid colsLandscape={3} colsPortrait={1} compact>
        <Tile mainText="🤕" subText="I FAILED" onClick={ doRecordFail } />
        <Tile mainText="🤩" subText="I PASSED" onClick={ doRecordPass } />
        <Tile mainText="❌" subText="CANCEL" onClick={ doCancel } />
      </ResponsiveGrid>
    </Section>

  </>); 

  function getTemplate(): Partial<IClimbInfo> {
    if (window.location.hash) {
      try {
        const json = JSON.parse(decodeURIComponent(window.location.hash.substring(1))); 
        if (typeof(json) === 'object')
          return json; 
      }
      catch (e) { console.log(e); }
    }

    const currentProfile = Profile.current; 
    const allClimbs = currentProfile.getClimbs(); 
    const currentDifficulty = allClimbs.length ? allClimbs[allClimbs.length - 1].difficulty : 1; 
    return { date: new Date(), successful: false, wall: "Demo Wall", difficulty: currentDifficulty, technique: '', attempt: 0, streak: 0 }; 
  }

  function doCancel() {
    window.history.back(); 
  }

  function doRecordFail() {
  }

  function doRecordPass() {
  }
}

export default RecordClimbContent;
