import { NavLink } from "react-router-dom";

// Note: we must use <Link to> or <NavLink to> instead of <a href> for internal links in order to have rerendering without actual page navigation. 
//       It will do the rest for us (rerendering the main BrowserRouter component and adjust the browser page URL via the URL API). 

function Navigation() {
  return (
    <nav className="navigation">
      <ul>
        <li><NavLink to="/" className={determineClass}>Dashboard</NavLink></li>
        <li><NavLink to="/climb" className={determineClass}>New Climb</NavLink></li>
        <li><NavLink to="/history" className={determineClass}>My History</NavLink></li>
        <li><NavLink to="/feedback" className={determineClass}>Feedback</NavLink></li>
        <li><NavLink to="/about#company" className={determineClass}>About</NavLink></li>
      </ul>
    </nav>
  ); 
}

function determineClass(props: { isActive: boolean }) {  // called by NavLink
  return props.isActive ? 'current' : ''; 
}

export default Navigation;
